export default {
  name: "load-view",
  data(){
    return {
      loading_time:1,
	  loading: false
    }
  },
	watch:{
		loading(val){
			if(val){
				// uni.hideTabBar()
				let timer=setInterval(() => {
						if(this.loading_time < 6){
							this.loading_time++
						}else{
							clearInterval(timer)
						}
					}, 1000);
			}
		}
	},
	methods: {
		show(){
			this.loading=true;
		},
		close(){
			this.loading_time=1
			this.loading=false
		}
	}
}
